.navbarcustom {
	background-color: rgb(253, 253, 253);
}

.customlink {
	font-size: 1.5em;
	transition: 0.2s;
	padding: 2px;
	text-decoration: none;
	color: #e3a7ff !important;
	font-weight: 150;
}

.customlink:hover {
	color: black !important;
}

.customlink2 {
	font-size: 1.5em;
	transition: 0.2s;
	padding: 2px;
	text-decoration: none;
	color: white;
	font-weight: 150;
}

.customlink2:hover {
	color: black !important;
}
.logolink1 {
	background-image: url(./images/Triage-Logo.png);
	height: 83.33px;
	width: 116.915px;
	object-fit: cover;
	background-size: cover;
}

.customh1 {
